<template lang="pug">
    .subjects__content
        .subjects__title Предметы
        ul.subjects__list
            li.subjects__item(v-for="subject in subjects" :key="subject.id")
                subject-item(
                    :subject='subject'
                )
        button(@click.prevent='addSubject' id='addSubject').add__subject Добавить предмет
        add-subject-form(
                :departments = "departments"
                v-if='addSubjectFormVisible'
                v-on:eventNewSubject='eventNewSubject'
            )

</template>
<script>
import {mapState, mapActions} from "vuex";
export default {
    components: {
        subjectItem: () => import("../../admin/subjects/subject-item"),
        addSubjectForm: () => import("../../admin/subjects/add-subject-form")
    },
    computed: {
        ...mapState("subjects",
        {
            subjects: state => state.subjects
        }
        ),
        ...mapState("departments",
        {
            departments: state => state.departments
        }
        )
    },
    data () {
        return {
            addSubjectFormVisible: false
        }
    },
    created() {
        this.fetchSubjects();
        this.fetchDepartments();
    },
    methods: {
        ...mapActions('subjects', ['fetchSubjects']),
        ...mapActions("departments", ["fetchDepartments"]),
        addSubject() {
            this.addSubjectFormVisible = !this.addSubjectFormVisible
            document.getElementById('addSubject').setAttribute("disabled", "true")

        },
        eventNewSubject() {
            this.addSubjectFormVisible=!this.addSubjectFormVisible
            document.getElementById('addSubject').removeAttribute("disabled")
      }
    }
}
</script>
