    position: relative;
    $animals: 'whale1', 'sheep', 'shark', 'penguin','owl1','butterfly1','bird7','cat1','dog2','giraffe1','fish5','butterfly3','camel1','bird4','hippopotamus2';
    @for $i from 1 through length($animals) {
        $item: nth($animals, $i);
        &:nth-child(15n+#{$i}){
            position: relative;
            &:after {
                content: '';
                display: block;
                position: absolute;
                background: url(../../../assets/images/icons/#{$item}.svg) center center no-repeat;
                background-size: contain;
                top: 0;
                left: 0;
                width: 45px;
                height: 45px;
                border-radius: 4px;
            }
        }
    }
    &:nth-child(even) {
        &:after {left: 43px;}}
}
.add__subject {
    padding: 1%;
